.gantt-task-foreground-sections {
    height: 100%;
    width: 100%;
    position: absolute;
    overflow: hidden;
}

.gantt-task-sections {
    height: 100%;
    width: 100%;
    position: absolute;
    overflow: hidden;
    bottom: 0;
}

.gantt-task-sections .gantt-task-section {
    position: absolute;
    overflow: hidden;
    height: 100%;
}



